.main-navigation {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  position: relative;
}
.main-navigation__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  width: 62px;
  height: 55px;
  cursor: pointer;
  border: none;
  background-color: #81B3D2;
}
.main-navigation--closed .main-navigation__toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  right: 18px;
  width: 23px;
  height: 4px;
  background-color: #81B3D2;
  box-shadow: 0 7px 0 0 #81B3D2, 0 14px 0 0 #81B3D2;
}
.main-navigation--closed .main-navigation__toggle:hover::before {
  background-color: rgba(129, 179, 210, 0.7);
  box-shadow: 0 7px 0 0 rgba(129, 179, 210, 0.7), 0 14px 0 0 rgba(129, 179, 210, 0.7);
}
.main-navigation--opened .main-navigation__toggle::before,
.main-navigation--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  top: 24px;
  right: 15px;
  width: 27px;
  height: 4px;
  background-color: #ffffff;
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}
.main-navigation__list {
  padding: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .main-navigation__wrapper {
    display: flex;
    margin: 0 auto;
  }
  .main-navigation__list {
    height: 56px;
  }
  .main-navigation__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .main-navigation__wrapper {
    width: 1200px;
  }
  .main-navigation__toggle {
    display: none;
  }
